* {
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace; }

ul {
  list-style: none;
  margin: 0;
  padding: 0; }

a {
  color: var(--text-color);
  text-decoration: none; }

input {
  border: none;
  background-color: none; }

.flex {
  display: flex; }

.container {
  margin: 0 auto;
  width: 80%; }

.grid {
  height: 70vh;
  gap: 10px;
  display: grid;
  grid-template-rows: repeat(3, 1fr);
  grid-template-columns: repeat(3, 1fr); }

.Header {
  height: 100px;
  overflow: visible;
  align-items: center;
  justify-content: space-between;
  padding: 20px;
  margin-bottom: 10px; }
  .Header li {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 5px 20px;
    text-align: center; }

.Item {
  width: 100%;
  border: 2px solid #000;
  flex-direction: column;
  align-items: center; }
  .Item h2 {
    width: 100%;
    padding: 5px;
    border-bottom: #000 solid 2px; }
  .Item img {
    margin: 10px auto;
    height: 150px;
    width: auto; }
  .Item p {
    font-size: 2rem;
    margin: 10px; }
  .Item input {
    margin: 10px;
    border: none;
    color: #000;
    background-color: #2da9be; }
    .Item input:focus {
      transform: scale(0.98);
      border: none;
      outline: none; }

.cart-item.flex {
  z-index: 2;
  width: 100px;
  padding: 5px 5px;
  flex-direction: column;
  align-items: center;
  padding: 0.5rem;
  justify-content: center;
  font-size: 2rem;
  color: #ffffff;
  transition: height 2s ease; }

.icon-button {
  width: 50px;
  height: 50px;
  background-color: #484a4d;
  border-radius: 50%;
  padding: 5px;
  margin: 2px;
  align-items: center;
  justify-content: center;
  transition: filter 300ms; }
  .icon-button:hover {
    filter: brightness(1.2); }
  .icon-button svg {
    fill: #fff;
    width: 20px;
    height: 20px; }

.dropdown {
  font-size: 1rem;
  align-items: center;
  justify-content: center;
  position: absolute;
  z-index: -1;
  top: 48px;
  width: 300px;
  transform: translateX(-42%);
  background-color: #484a4d;
  border: none;
  border-radius: 5px;
  border-top-right-radius: 0px;
  overflow: hidden;
  transition: height 500ms ease;
  padding: 1rem; }
  .dropdown .menu .flex {
    justify-content: center;
    align-items: center; }
    .dropdown .menu .flex input {
      background-color: #dddd;
      padding: 5px 10px;
      text-align: center;
      height: 40px; }
      .dropdown .menu .flex input:focus {
        outline: none; }
    .dropdown .menu .flex p {
      margin: 10px 0px; }
  .dropdown a {
    display: block; }
  .dropdown p {
    margin: 5px auto;
    padding: 0px 3px;
    width: 90px; }
  .dropdown .cart-comp {
    display: flex;
    font-size: 1rem;
    margin: 10px; }
    .dropdown .cart-comp input {
      background-color: #dddd;
      text-align: center; }
      .dropdown .cart-comp input:focus {
        outline: none; }

.relate {
  position: relative; }

.menu {
  width: 100%; }

.menu-primary-enter {
  position: absolute;
  transform: translateX(-110%); }

.menu-primary-enter-active {
  transform: translateX(0%);
  transition: all 500ms ease; }

.menu-primary-exit {
  position: absolute; }

.menu-primary-exit-active {
  transform: translateX(-110%); }

.menu-secondary-enter {
  position: absolute;
  transform: translateX(110%); }

.menu-secondary-enter-active {
  transform: translateX(0%);
  transition: all 500ms ease; }

.menu-secondary-exit {
  position: absolute; }

.menu-secondary-exit-active {
  transform: translateX(110%); }
